import apiInstance from './index';

const api = apiInstance();

const defaultSuccess = (res) => {};
const defaultFail = (err) => {};

/*
	    NFT   CRUD API    
	*/

/**
 *    
 * @param {Function} success       
 * @param {Function} fail          
 * @returns {Promise}
 */
const getItems = async () => {
  let result = null;
  await api
    .get('/items')
    .then((res) => {
      result = res;
    })
    .catch((err) => {});
  return result;
};

/**
 *    
 * @param {*} userAddr
 * @param {Function} success       
 * @param {Function} fail          
 */
const getUsersItems = async (userAddr) => {
  let result = null;
  await api
    .get(`/items/${userAddr}`)
    .then((res) => {
      result = res;
    })
    .catch((err) => {});
  return result;
};

/**
 *    
 * @param {Function} success       
 * @param {Function} fail          
 */
const getItemsOnSale = (success = defaultSuccess, fail = defaultFail) => {
  api.get('/items/onSale').then(success).catch(fail);
};

/**
 * NFT 
 * @param {*} tokenUri
 * @param {*} ownerAddr
 * @param {*} tokenId
 * @param {Function} success       
 * @param {Function} fail          
 */
const addItem = async (
  donationSeq,
  tokenUri,
  ownerAddr,
  tokenId,
  success = defaultSuccess,
  fail = defaultFail,
) => {
  const data = {
    donationSeq: donationSeq,
    imageUrl: tokenUri,
    ownerAddress: ownerAddr,
    tokenId: tokenId,
  };

  await api.post('/items/add', JSON.stringify(data)).then(success).catch(fail);
};

/**
 * NFT  
 * @param {*} newOwnerAddr
 * @param {*} tokenId
 * @param {Function} success       
 * @param {Function} fail          
 */
const updateNftOwner = (
  newOwnerAddr,
  tokenId,
  success = defaultSuccess,
  fail = defaultFail,
) => {
  const data = {
    ownerAddress: newOwnerAddr,
    tokenId: tokenId,
  };

  api.patch('/items/update', JSON.stringify(data)).then(success).catch(fail);
};

/**
 *  
 * @param {Function} success       
 * @param {Function} fail          
 */
const updateItemLikes = (success = defaultSuccess, fail = defaultFail) => {
  api.patch('/items').then(success).catch(fail);
};

/**
 *   
 * @param {Function} token_id
 * @param {Function} success       
 * @param {Function} fail          
 * @returns {Promise}
 */
const getItem = async (
  token_id,
  success = defaultSuccess,
  fail = defaultFail,
) => {
  let result = null;
  await api
    .get(`/items/details/${token_id}`)
    .then((res) => {
      result = res.data;
    })
    .catch(fail);
  return result;
};

export {
  getItems,
  getUsersItems,
  getItemsOnSale,
  addItem,
  updateNftOwner,
  updateItemLikes,
  getItem,
};
