import Web3 from 'web3';
import CallAPI from './API.js';

const callApiList = [
  'transferFrom', // NFT  => ownerAddr, tokenId 
  'createSale', //  
  'bid', // 
  'purchase', //  
  'confirmItem', //  
  'cancelSales', //  
];

// Web3
const web3 = new Web3(
  new Web3.providers.HttpProvider(process.env.VUE_APP_ETHEREUM_RPC_URL),
);

/**
 *     
 *     .
 * @param {String} fromAddr  
 * @param {String} privKey   
 * @param {String} toAddr  
 * @param {String} data  
 * @returns {Promise}   
 */
export default async function sendTransaction(
  fromAddr,
  privKey,
  toAddr,
  data,
  tokenId = null,
) {
  const gas = await data.estimateGas({ from: fromAddr });

  //  
  const tx = {
    gas: gas,
    to: toAddr,
    data: data.encodeABI(),
  };

  let result = null;

  // 
  await web3.eth.accounts.signTransaction(tx, privKey).then(async (rawTx) => {
    //  
    await web3.eth
      .sendSignedTransaction(rawTx.rawTransaction)
      .once('receipt', (receipt) => {
        result = receipt;
      })
      .on('confirmation', async (confirmationNumber) => {
        if (confirmationNumber == 1) {
          // console.log('start');

          // API     
          if (callApiList.includes(data._method.name)) {
            if (tokenId) {
              await CallAPI(
                data._method.name,
                data.arguments,
                fromAddr,
                tokenId,
              );
            } else {
              await CallAPI(data._method.name, data.arguments, fromAddr);
            }
          }
        } else if (confirmationNumber == 3) {
          // console.log('confirm end!');
        }
      })
      .on('error', (error) => {
        console.log(error);
      });
  });
  return result;
}
