/*
	        Ethereum Net         
	*/

import Web3 from 'web3';

const web3 = new Web3(
  new Web3.providers.HttpProvider(process.env.VUE_APP_ETHEREUM_RPC_URL),
);

/**
 *   .
 * @param {String} privKey 
 * @returns 
 */
const getAddressFrom = (privKey) => {
  if (privKey.length === 66 && privKey.startsWith('0x')) {
    const pubKey = web3.eth.accounts.privateKeyToAccount(privKey);
    return pubKey.address;
  } else {
    throw '  .';
  }
};

/**
 *   .
 * @param {String} txHash     
 * @returns {Number}           / 0: / 1: / 2:
 */
const getTxStatus = async (txHash) => {
  let result = null;
  await web3.eth
    .getTransactionReceipt(txHash)
    .then(async (res) => {
      if (res.status) {
        console.log('success');
        result = 2;
      } else {
        await web3.eth
          .getPendingTransactions()
          .then((res) => {
            for (let tx of res) {
              if (tx.hash === txHash) {
                console.log('pending');
                result = 1;
              }
            }
            console.log('fail');
            result = 0;
          })
          .catch((err) => console.log(err));
      }
    })
    .catch((err) => console.log(err));

  return result ? result : new Error(':    ');
};

export { getAddressFrom, getTxStatus };
